<template>
  <div id="app">
    <transition name="fade">
      <keep-alive>
        <router-view></router-view>
      </keep-alive>
    </transition>
  </div>
</template>

<script>
  export default {
    name: 'app',
    data () {
      return {
        isLogin: false,
        userInfo: { // 
          nick: null,
          ulevel: null,
          uid: null,
          portrait: null
        }
      }
    },
    mounted () {
      // 
      this.getUserInfo();
    },
    methods: {
      // 
      getUserInfo () {
        // http
        this.userInfo = {
          nick: 'Doterlin',
          ulevel: 20,
          uid: '10000'
//          portrait: 'images/profile.png'
        };

        // 
        // (vue-resource):
//        ts.$http.get(url, {
//          // 
//          "params":{}
//        }).then((response) => {
//          // Success
//        }, (response) => {
//          // Error
//        });

        // mutationStore
        this.$store.commit('updateUserInfo', this.userInfo);
      }
    }
  };
</script>

<style>
  #app {
    width: 100%;
    background-color: #fff;
  }
</style>



